import React from 'react'
import { Link } from 'react-router-dom'

function TrackInfoC(props) {
    return (
        <div className="trackinfo-c">
            <Link to={"/"}><button><i class="fas fa-caret-left"></i> Go Back</button></Link>
            <img src={props.image} alt=""/>
            <span>{props.title}</span>
            <pre>{props.lyrics}</pre>
        </div>
    )
}

export default TrackInfoC

