import React,{useContext, useState, useEffect} from 'react'
import { StateContext } from './../../ContextAPI';
import Track from './Track';


function Tracks() {
    const value = useContext(StateContext)
    const [Content, setContent] = useState(false)

    useEffect(() => {
        if(value.trackList === undefined || value.trackList.length === 0){
            setContent(false)
        }
        else{
            setContent(true)
        }
    },[value])

    return (
        <>
        {Content ? 
        value.trackList.map(track => (
        <Track 
            key={track.result.id} 
            img={track.result.header_image_thumbnail_url} 
            name={track.result.full_title} 
            artist={track.result.primary_artist.name} 
            id={track.result.id} 
            title={track.result.title}/>
        )) 
        : null}
        </>
    )
}

export default Tracks
