import React, {useContext, useEffect, useState} from 'react'
import { StateContext } from './../../ContextAPI';

function Search() {
    const value = useContext(StateContext)
    const [style, setStyle] = useState('search-c')

    useEffect(() => {
        if (value.search !== ''){
            setStyle('search-c valid')
        }
    },[value.search])

    function handleChange(e){
        value.setSearch(e.target.value)
    }

    return (
        <div className={style}  >
            <span className={value.search ? "valid" : null}><i className="fas fa-music"></i> Search For a Song</span>
            <input placeholder="Song, Artist, Album..." type="text" name="search" value={value.search} onChange={handleChange}/>
        </div>
    )
}

export default Search
