import React, {useState, useEffect} from 'react'
import axios from 'axios'
import Loader from '../layout/Loader'
import { getLyrics } from 'genius-lyrics-api';
import TrackInfoC from './TrackInfoC';

function TrackInfo(get) {
    const [TrackInfo, setTrackInfo] = useState({})
    const [Lyrics, setLyrics] = useState('')
    const [Loading, setLoading] = useState(true)
    
    useEffect(() => {
        const options = {
            method: 'GET',
            url: `https://genius.p.rapidapi.com/songs/${get.match.params.id}`,
            headers: {
              'x-rapidapi-key': '33362ce81dmshd6ba172044c8d10p12e31fjsn5a5017e5ceba',
              'x-rapidapi-host': 'genius.p.rapidapi.com'
            }
          };
        axios.request(options)
        .then(res => {
            setTrackInfo(res.data.response.song)    
            getLyrics('http://cors-anywhere.herokuapp.com/'+res.data.response.song.url)
            .then(lyrics => {
                setLoading(false)
                setLyrics(lyrics)
            })
        })
    })

    return (
        <>
            {Loading ? <Loader /> : <TrackInfoC image={TrackInfo.header_image_url} title={TrackInfo.title} lyrics={Lyrics} />}
            
        </>
    )
}

export default TrackInfo
